import React from 'react';
import {
    Panel,
    PanelHeader,
    IOS,
    platform,
    PanelHeaderButton,
    Div,
    Button,
    Gallery,
    FormLayout,
    Select
} from '@vkontakte/vkui';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28MarketAddBadgeOutline from '@vkontakte/icons/dist/28/market_add_badge_outline';

import tovar_2 from '../img/tovar_2.jpg';

const osname = platform();

class TestTovar2 extends React.Component {
    constructor (props) {
        super(props);

        this.state = {};
    }

render () {
    const {
        id,
        go,
    } = this.props;

return (
    <Panel id={id} separator={false}>
        <PanelHeader
            left={
                <PanelHeaderButton onClick={go} data-to="store">
                    {osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
                </PanelHeaderButton>
            }
        >
            Персик
        </PanelHeader>
        <img className="open-tovar" type="image" src={tovar_2}/>
            <Div>
                <div className="text-open-tovar zapret-videleniya">
                    <h1>Персик</h1>
                </div>
                <div className="money-open-tovar zapret-videleniya">
                    <h3>150 баллов</h3>
                </div>
                <div className="category-open-tovar zapret-videleniya">
                    Мягкие игрушки
                </div>
                <Div className="description-open-tovar zapret-videleniya">
                    Плюшевая игрушка «Персик».
                    <br></br><br></br>Обратите внимание: из-за ограничительных мер время доставки может увеличиться до 30 дней с момента подтверждения. Количество товара в рамках месяца ограничено.
                </Div>
            </Div>
            <Div><Button size="xl" before={<Icon28MarketAddBadgeOutline />}>Добавить в корзину</Button></Div>
        </Panel>
        );
    }
}

export default TestTovar2;