import React from 'react';
import {
    Panel,
    PanelHeader,
    IOS,
    platform,
    PanelHeaderButton,
    Group,
    Div,
    Header,
    List,
    Cell,
    Button
} from '@vkontakte/vkui';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import tovar_2 from '../img/tovar_2.jpg';
import tovar_1 from '../img/tovar_1.jpg';

const osname = platform();

class TestActiveTovar extends React.Component {
    constructor (props) {
        super(props);

        this.state = {};
    }

render () {
    const {
        id,
        go,
    } = this.props;

return (
    <Panel id={id} separator={true}>
        <PanelHeader
            left={
                <PanelHeaderButton onClick={go} data-to="orders">
                    {osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
                </PanelHeaderButton>
            }
        >
            Заказ #123
        </PanelHeader>
            <Group header={<Header mode="secondary">Дата заказа</Header>}>
                <Div>
                    25.07.2020 в 15:20
                </Div>
            </Group>
            <Group header={<Header mode="secondary">Статус</Header>}>
                <Div>
                    На рассмотрении
                </Div>
            </Group>
            <Group header={<Header mode="secondary">Товары</Header>}>
                <List>
                    <Cell
                        before={
                            <div className="ico-align">
                                <img
                                    className="ico-crop"
                                    type="image"
                                    src={tovar_2}
                                />
                            </div>
                        }
                        multiline
                    >
                        <span>Спаркс</span>
                        <span className="disable-selectionn text">
                            Цена: 
                            150 баллов
                        </span>
                        <span className="disable-selectionn text">
                            Категория:
                            Мягкие игрушки
                        </span>
                        <span className="disable-selectionn text">
                            Количество:
                            x1
                        </span>
                    </Cell>

                    <Cell
                        before={
                            <div className="ico-align">
                                <img
                                    className="ico-crop"
                                    type="image"
                                    src={tovar_1}
                                />
                            </div>
                        }
                        multiline
                    >
                        <span>Стикеры</span>
                        <span className="disable-selectionn text">
                            Цена: 
                            55 баллов
                        </span>
                        <span className="disable-selectionn text">
                            Категория:
                            Сувениры
                        </span>
                        <span className="disable-selectionn text">
                            Количество:
                            x1
                        </span>
                        <span className="disable-selectionn text">
                            Набор:
                            Сеня
                        </span>
                    </Cell>
                </List>
            </Group>
            <Group header={<Header mode="secondary">Способ доставки</Header>}>
                <Div>Доставка</Div>
            </Group>
            <Group header={<Header mode="secondary">Контактный телефон</Header>}>
                <Div>+7 123 456 78 90</Div>
            </Group>
            <Group header={<Header mode="secondary">Адрес</Header>}>
                <Div>Россия, Москва, ул. Пушкина, д. 123, кв. 4, корпус 5, индекс: 678910</Div>
            </Group>
            <Group header={<Header mode="secondary">Получатель</Header>}>
                <Div>Тихонович Александр Дмитриевич</Div>
            </Group>
            <Group header={<Header mode="secondary">Детали заказа</Header>}>
                <Div>-</Div>
            </Group>
            <Group>
                <Div><Button size="xl" mode="secondary">Отменить заказ</Button></Div>
            </Group>
        </Panel>
        );
    }
}

export default TestActiveTovar;