import React from 'react';
import {
    Panel,
    PanelHeader,
    IOS,
    platform,
    PanelHeaderButton,
    Div,
    Button,
    Gallery,
    FormLayout,
    Select
} from '@vkontakte/vkui';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28MarketAddBadgeOutline from '@vkontakte/icons/dist/28/market_add_badge_outline';

import tovar_1 from '../img/tovar_1.jpg';
import tovar_1_2 from '../img/tovar_1_2.jpg';
import tovar_1_3 from '../img/tovar_1_3.jpg';
import tovar_1_4 from '../img/tovar_1_4.jpg';
import tovar_1_5 from '../img/tovar_1_5.jpg';

const osname = platform();

class TestTovar1 extends React.Component {
    constructor (props) {
        super(props);

        this.state = {};
    }

render () {
    const {
        id,
        go,
    } = this.props;

return (
    <Panel id={id} separator={false}>
        <PanelHeader
            left={
                <PanelHeaderButton onClick={go} data-to="store">
                    {osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
                </PanelHeaderButton>
            }
        >
            Стикеры
        </PanelHeader>
        <Gallery
            slideWidth="100%"
            className="open-tovar"
            bullets="dark"
        >
        <img type="image" src={tovar_1}/>
        <img type="image" src={tovar_1_2}/>
        <img type="image" src={tovar_1_3}/>
        <img type="image" src={tovar_1_4}/>
        <img type="image" src={tovar_1_5}/>
        </Gallery>
            <Div>
                <div className="text-open-tovar zapret-videleniya">
                    <h1>Стикеры</h1>
                </div>
                <div className="money-open-tovar zapret-videleniya">
                    <h3>45 баллов</h3>
                </div>
                <div className="category-open-tovar zapret-videleniya">
                    Сувениры
                </div>
                <Div className="description-open-tovar zapret-videleniya">
                    Наклейки с героями популярных стикеров ВКонтакте. Теперь любимые персонажи всегда будут с вами — украшайте ими ноутбуки, телефоны, тетради и любые другие поверхности! Размер каждого листа — 21х15 см.
                    12 стикеров на листе.
                    <br></br><br></br>Обратите внимание: из-за ограничительных мер время доставки может увеличиться до 30 дней с момента подтверждения. Количество товара в рамках месяца ограничено.
                </Div>
            </Div>
            <FormLayout>
                <Select top="Набор" placeholder="Выберите набор">
                    <option value="XS">Сеня</option>
                    <option value="S">Авокадик</option>
                    <option value="M">Бананос</option>
                    <option value="L">Спаркс</option>
                    <option value="XL">Дигги</option>
                </Select>
            </FormLayout>
            <Div><Button size="xl" before={<Icon28MarketAddBadgeOutline />}>Добавить в корзину</Button></Div>
        </Panel>
        );
    }
}

export default TestTovar1;